.help-container {
  margin-top: 110px;
}

.help-content {
  margin-left: 250px;
  padding: 10px 100px;
}
.help-header {
  margin-top: 0px;
  margin-left: 100px;
  width: 80%;
  font-size: 30px;
  font-weight: 900;
  text-align: center;
  color: #074979;
}

.help-search {
  display: flex;
  justify-content: center;
}

.help-popular-tag {
  margin: 16px 0;

  display: flex;
  justify-content: center;
  align-items: center;
}

.help-popular-item {
  display: flex;
  align-items: center;
  background-color: #dae4eb;
  font-weight: 500;
  padding: 8px 16px;
  font-size: 14px;
  margin: 0 12px;
  border: 1px solid #074979;
  border-top-right-radius: 20px;
  border-bottom-right-radius: 20px;
  min-width: 80px;
  text-align: center;
}

.help-popular-item-icon {
  margin-right: 12px;
}

.help-popular-item:hover {
  background-color: #9cb6c9;
  cursor: pointer;
}

.help-item {
  background-color: #dae4eb;
  width: 45%;
  text-align: center;
  min-height: 200px;
  margin: 16px 0;
  border-radius: 30px;
  /* padding: 30px 0; */
}

.help-list {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
}

.help-icon {
  margin-top: 40px;
}

.help-item-header {
  margin: 16px 0;
  font-weight: 500;
  font-size: 20px;
}

.help-send-fb {
  margin: 16px 0;
}

.help-send-fb-header {
  font-size: 18px;
  font-weight: 500;
}
